import React from 'react';
import './App.css';
import {
  Route,
  NavLink,
  BrowserRouter,
} from "react-router-dom";
import about from './about';
import registration from './registration';
import contact from './contact';
import syllabus from './syllabus';
import brassPage from './brass'; 
import guitarPage from './guitar'; 
function App() {
  return (
    <body>
      <BrowserRouter>
      <div class = "header"> 
      <h2 id = "title"><a href=".">Kiwanis Music Festival of London</a></h2> 
      <nav id = "nav">
          <ul>
            <li><a href=".">Home</a></li>
            <li>
              <NavLink to="/contact">Contact Us</NavLink>
            </li>
            <li>
              <NavLink to="/syllabus">Syllabus</NavLink>
            </li>
            <li>
              <NavLink to="/about">About Us</NavLink>
            </li>
            <li>
              <NavLink to="/login">Login</NavLink>
            </li>
            <li >
              <NavLink to="/registration">Register</NavLink>
            </li>
          </ul>
        </nav>
      </div>
      <div id = "pages">
        <Route exact path="/syllabus/brass" component={brassPage} />
        <Route exact path="/syllabus/guitar" component={guitarPage}/>
        <Route exact path="/syllabus/harp" />
        <Route exact path="/syllabus/theatre" />
        <Route exact path="/syllabus/drums" />
        <Route exact path="/syllabus/piano" />
        <Route exact path="/syllabus/pipeorgan" />
        <Route exact path="/syllabus/speecharts" />
        <Route exact path="/syllabus/ukelele" />
        <Route exact path="/syllabus/singing" />
        <Route exact path="/syllabus/woodwinds" />
        <Route exact path="/syllabus/adult" />
        <Route exact path="/syllabus/singing" />
        <Route exact path="/about" component={about}/>
        <Route exact path="/registration" component={registration}/>
        <Route exact path="/contact" component={contact}/>
        <Route exact path="/syllabus" component={syllabus}/>
        <Route exact path="/login" />
      </div>
    </BrowserRouter>
  </body>
  );
}

export default App;
